import { Component, NgZone } from '@angular/core';
import { NavController, Platform, AlertController, ToastController , Events, ModalController} from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { ChangeDetectorRef } from '@angular/core';
import { Storage } from '@ionic/storage';

import { EditTextModal } from '../../modals/edit-text/edit-text';

import { Clipboard } from '@ionic-native/clipboard';

import { SocialSharing } from '@ionic-native/social-sharing';

import { TranslateProvider } from '../../providers/translate/translate';

declare var window: any;
declare var audioinput: any;
declare var AudioHandler: any;

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
    
    public properties: any;
    public notes: Array<any>;
    public languages: Array<any>;
    public recognitionObject: any;
    public settings:any;

    constructor(public navCtrl: NavController, private plt: Platform, private cd: ChangeDetectorRef, public  _zone: NgZone, private storage: Storage, private alertCtrl: AlertController, private clipboard: Clipboard, private sharer: SocialSharing, private toastCtrl: ToastController, public events: Events, public modalCtrl: ModalController, private translateProvider: TranslateProvider) {
        this.properties = {isRecording:false, language: "EN", isIos: this.plt.is('ios'), previousDelete:false, previousNotePosition:0,
        translations:[], 
        currentMatch:"", 
        partialMatch:"", errorRestarting:false, micVolume:0, currentSaved:false, currentId:false};
        
        

        
        this.notes = [];
        this.languages = [];
        
        
        this.settings = {language:"", translations:[], historyMax:10};
        
        this.storage.get("settings").then((data) => {
            if (data){
                this.settings = data;
                this.properties.language = this.settings.language;
                this.updateSettingsTranslations();
            }
        })         
        
        this.storage.get("notes").then((data) => {
            if (data){
                this.notes = data;
                this.scrollBottom();
            }
        });
        
        this.setSupportedLanguages();
        
        
        
        this.plt.ready().then(() => {
            console.log(window);
            if(window.SpeechRecognition){
                this.setupRecognition();                   
                            
            }     
        });
        
        events.subscribe('notes:updated', (notes) => {
            this.notes = notes;
        });        

        events.subscribe('settings:updated', (settings) => {
            this.settings = settings;
            this.updateSettingsTranslations();
        });         
                
    }
    
    private setupRecognition(){
        this.recognitionObject = new window.SpeechRecognition(); 
        
        this.recognitionObject.continuous = true;
        this.recognitionObject.lang = this.properties.language;
        this.recognitionObject.maxAlternatives = 3;

        this.recognitionObject.onnomatch = (event => {
            console.log('No match found.');
        });  
        
        
        this.recognitionObject.onstart = (event => {
            console.log('Started recognition.');

            this.properties.errorRestarting = false;

        });  
        
        this.recognitionObject.onend = (event => {
            console.log('Stopped recognition.');
            this.properties.partialMatch = "";
            if (this.properties.isRecording){
                console.log("Attempting to restart.");
                setTimeout(() => {this.recognitionObject.start();},300);
                let errorCount  = 0;
                let errorLoop = setInterval(() => {
                    console.log("is error loop");
                    if (errorCount > 10){
                        //recognition service crash, restart it
                        console.log("service crashed");
                        //setTimeout(() => {this.recognitionObject.start();},200);
                        clearInterval(errorLoop);
                    }
                    else if (this.properties.errorRestarting){
                        this.recognitionObject.start();
                    }
                    else{
                        console.log("closing error loop");
                        clearInterval(errorLoop);
                    }
                    errorCount += 1;
                    
                },300);
            }
        });
        
        
        this.recognitionObject.onerror = (event => {
            console.log('Error...' + event.error);
            this.properties.partialMatch = "";
            if (event.error === "RecognitionService busy"){
                console.log("setting error property to true");
                this.properties.errorRestarting = true;
            }

            
        });     
        
        
        this.recognitionObject.onresult = (event => {
            
            if (event.results) {
                console.log("Results found");
                console.log(event);     
                this.properties.partialMatch = "";           
                let result = event.results[0][0].transcript;
                this._zone.run(() => {
                    this.properties.currentMatch = this.properties.currentMatch ? this.properties.currentMatch + " " + (result.charAt(0).toUpperCase() + result.slice(1)) + "." : result + ".";
                    this.calculateTranslations();
                    this.scrollBottom();
                    console.log(this.properties.currentMatch);
                });

            }                
       
            
        });      
        
        
        this.recognitionObject.onpartial = (event => {

            if (event.results) {
                console.log("Partial results found");
                console.log(event);                
                let result = event.results[0][0].transcript;
                this._zone.run(() => {
                    this.properties.partialMatch = result;
                    this.scrollBottom();
                });

            }     
            
        });            
                         
               
    }

    
    public toggleListening(){
        if (this.properties.isRecording){
            this.properties.isRecording = false;
            this.properties.errorRestarting = false;
            setTimeout(() => {AudioHandler.unmuteApp();},500);

        }
        else{
            if (this.properties.currentMatch){
                console.log(this.properties.currentMatch);
                this.notes.unshift({note: this.properties.currentMatch, saved: this.properties.currentSaved, id: this.properties.currentId, language: this.properties.language, translations: this.properties.translations});
                if (this.notes.length > this.settings.historyMax){
                    this.notes.pop();
                }
                this.storage.set("notes", this.notes);  
                this.scrollBottom();
                this.properties.currentMatch = "";  
                this.properties.translations = [];
                this.updateSettingsTranslations();
                this.properties.currentSaved = false;
                this.properties.currentId = false;
            }      
            this.properties.isRecording = true;
            this.recognitionObject.start(); 
            AudioHandler.muteApp();  
            
                        
        }
    }
    
    private updateSettingsTranslations(){
        let requireUpdate = false;
        for (let translation of this.settings.translations){
            let exists = false;
            for (let propertyTranslation of this.properties.translations){
                if (propertyTranslation.code === translation){
                    exists = true;
                    break;
                }
            }
            if (!exists){
                this.properties.translations.push({code:translation, text:this.properties.currentMatch});
                requireUpdate = true;
            }
        }        
        
        if (requireUpdate){
            this.calculateTranslations();
        }
    }
    
    public openEditMatch(){
        let modal = this.modalCtrl.create(EditTextModal, {text:this.properties.currentMatch});
        modal.onDidDismiss(data => {
            if (data){
                this.properties.currentMatch = data;
                this.calculateTranslations();
            }
        });
        modal.present();      
    }
    
    public copyNote(note){
        console.log("copying");
        this.clipboard.copy(note).then(() => {
            let toast = this.toastCtrl.create({
              message: 'Note copied to clipboard',
              duration: 1000,
              position: 'bottom'
            }); 
            toast.present();           
        });
    }
    
    public shareNote(note){
        console.log("sharing");
        this.sharer.share(note);
    }
    
    public saveNote(note){
        console.log("saving");
        if (this.properties.currentSaved){
            this.properties.currentSaved = false;
            
            this.storage.get("savedNotes").then((notes) => {
                if (notes){
                    for (var index in notes){
                        let savedNote = notes[index];
                        if (savedNote.id === this.properties.currentId){
                            notes.splice(index,1);
                            this.storage.set("savedNotes", notes);
                            this.properties.currentId = false;
                            break;
                        }
                    }
                }

                this.storage.set("savedNotes", notes);
                  
                
            });            
            
            
        }
        else{
            this.properties.currentSaved = true;

            this.storage.get("savedNotes").then((notes) => {
                if (!notes){notes = []};
                
                this.properties.currentId = notes.length + 1;
                
                notes.unshift({note:this.properties.currentMatch, id:this.properties.currentId, language:this.properties.language, translations: this.properties.translations});
                
                this.storage.set("savedNotes", notes);
               
                let toast = this.toastCtrl.create({
                  message: 'Note saved',
                  duration: 1000,
                  position: 'bottom'
                }); 
                toast.present();                   
                
            }); 
            
              
        }     
    }
        
    public savePreviousNote(note){
        if (note.saved){
            note.saved = false;
            
            this.storage.get("savedNotes").then((notes) => {
                if (notes){
                    for (var index in notes){
                        let savedNote = notes[index];
                        if (savedNote.id === note.id){
                            notes.splice(index,1);
                            this.storage.set("savedNotes", notes);
                            note.id = false;
                            this.storage.set("notes", this.notes);
                            break;
                        }
                    }
                }
                                  
                
            });              
            
            
            
        }
        else{
            note.saved = true;
            
            this.storage.get("savedNotes").then((notes) => {
                if (!notes){notes = []};
                
                note.id = notes.length + 1;
                
                notes.unshift({note:note.note, id:note.id, language:note.language, translations: note.translations});
                
                this.storage.set("notes", this.notes);
                this.storage.set("savedNotes", notes);
               
                let toast = this.toastCtrl.create({
                  message: 'Note saved',
                  duration: 1000,
                  position: 'bottom'
                }); 
                toast.present();                   
                
            });
        }        
    }
 
    
    
    private scrollBottom(){
        var objDiv = document.querySelectorAll(".dictate-card p");
        for (var index in objDiv){
            console.log(objDiv[index]);
            if (objDiv[index] instanceof HTMLElement){
                objDiv[index].scrollTop = objDiv[index].scrollHeight;
            }
        }
    }
    
    public dragNote(ev,index,note){
        let element = ev._elementRef.nativeElement;
        element.style.opacity = 1 - (ev._openAmount / 200);
        this.properties.previousNotePosition = ev._openAmount;
        if (ev._openAmount >= (100) && !this.properties.previousDeleted){
            this.properties.previousDeleted = true;
            setTimeout(() => {
                if (this.properties.previousNotePosition < 100){return;}
                this.notes.splice(index,1);
                this.storage.set("notes", this.notes);
                let toast = this.toastCtrl.create({
                  message: 'Note removed',
                  duration: 1000,
                  position: 'bottom'
                }); 
                toast.present();  
                this.properties.previousDeleted = false; 
            },500);          
        }
        
    }
    

    
    public openTranslateLanguage(){
        let data = {
            title: "Add Translations",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'OK',
                    handler: data => {
                       console.log(data);
                       
                       
                       //first check for removed items
                       for (var i = this.properties.translations.length - 1; i > -1; i--){
                           let translation = this.properties.translations[i];
                           if (data.indexOf(translation.code) < 0){
                               this.properties.translations.splice(i,1)
                           }
                       }
                       
                       let requireUpdate = false;
                       for (let code of data){
                           if (!this.isTranslationChecked(code)){
                               this.properties.translations.push({code: code, text: this.properties.currentMatch});
                               requireUpdate = true;
                           }
                       }
                       
                       if (requireUpdate){
                            this.calculateTranslations();
                       }
                                              
                   }
                }
            ],
            inputs: []
        }
        
        for (let language of this.languages){
            data.inputs.push({type: 'checkbox', label: language.name, value: language.code, checked: this.isTranslationChecked(language.code)})
        }
        
        let alert = this.alertCtrl.create(data);
        alert.present();         
    }
    
    public removeTranslation(index, translationArray){
        translationArray.splice(index,1);
        this.storage.set("notes", this.notes);
    }
    
    public calculateTranslations(){
        for (let translation of this.properties.translations){
            this.translateProvider.translateText(this.properties.language, translation.code, this.properties.currentMatch).then((result) => {
                translation.text = result["data"]["translations"][0]["translatedText"];
                this.scrollBottom();
            }).catch((e) => {
                console.log(e);
            })            
        }
        
        
        
        
        
    }
    
    private isTranslationChecked(language){
        let checked = false;
        for (let translation of this.properties.translations){
            if (translation.code === language){
                checked = true;
                break;
            }
        }
        return checked;
    }
    
        
    
    public changeLanguage(){
        let data = {
            title: "Change Language",
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel'
                },
                {
                    text: 'OK',
                    handler: data => {
                       console.log(data);
                       this.properties.language = data;
                       if (this.recognitionObject){
                        this.recognitionObject.lang = this.properties.language;
                       }
                       this.getLocallyTranslatedLanguages();
                   }
                }
            ],
            inputs: []
        }
        
        for (let language of this.languages){
            data.inputs.push({type: 'radio', label: language.name, value: language.code, checked:language.code === this.properties.language})
        }
        
        let alert = this.alertCtrl.create(data);
        alert.present();        
    }
    
    
    
    public formatLanguage(code){
        let formatted = "";
        for (let language of this.languages){
            if (language.code === code){
                formatted = language.name;
                break;
            }
        }
        return formatted;
    }

    private setSupportedLanguages(){
        this.languages = [
            {code: "ZH", name: "Chinese (Mandarin)"},
            {code: "CS", name: "Czech"},
            {code: "DA", name: "Danish"},
            {code: "NL", name: "Dutch"},
            {code: "EN", name: "English"},
            {code: "ET", name: "Estonian"},
            {code: "FR", name: "French"},
            {code: "KA", name: "Georgian"},
            {code: "DE", name: "German"},
            {code: "HI", name: "Hindi"},
            {code: "HU", name: "Hungarian"},
            {code: "LA", name: "Latin"},
            {code: "LV", name: "Latvian"},
            {code: "LT", name: "Lithuanian"},
            {code: "SR", name: "Serbian"},
            {code: "CY", name: "Welsh"},
            {code: "XH", name: "Xhosa"},

        ];
        
        this.languages.sort(function(a, b){
            return a.name == b.name ? 0 : +(a.name > b.name) || -1;;
        });
        
        
        this.storage.set("languages", this.languages);
        
        this.getLocallyTranslatedLanguages();

        
    }
    
    
    private getLocallyTranslatedLanguages(){
        this.translateProvider.getLanguages(this.properties.language).then((result) => {
            console.log(result["data"]["languages"]);
            let languages = [];
            for (let language of result["data"]["languages"]){
                languages.push({name:language.name, code:language.language.toUpperCase()});
            }
            console.log(languages);
            this.languages = languages;
            this.storage.set("languages", this.languages);
        })        
    }
    
}



