import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';

/*
  Generated class for the AuthenticationProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class TranslateProvider {

  constructor(public http: HttpClient,  public storage: Storage) {

  }
    
  
  
    public translateText(srcLanguage,targetLanguage, text){
        return new Promise((resolve, reject) => {
            
            let data = { source: srcLanguage, target: targetLanguage, format:"text", q:text, key:"AIzaSyDqS-gcwHt33Z-8aFAPQYLlxolrJ_Knr_8" };
            
            this.http.post("https://translation.googleapis.com/language/translate/v2?key=AIzaSyDqS-gcwHt33Z-8aFAPQYLlxolrJ_Knr_8", data).subscribe((res) => {
                resolve(res);                
                
            },(e) => {
                reject(e);
            });


     
        })        
    }
    
    
    
    public getLanguages(srcLanguage){
        return new Promise((resolve, reject) => {
            
            let data = {key: "AIzaSyDqS-gcwHt33Z-8aFAPQYLlxolrJ_Knr_8", target:srcLanguage};
            
            this.http.post("https://translation.googleapis.com/language/translate/v2/languages?key=AIzaSyDqS-gcwHt33Z-8aFAPQYLlxolrJ_Knr_8", data).subscribe((res) => {
                resolve(res);                
                
            },(e) => {
                reject(e);
            });


     
        })        
    }    





}



