import { Component } from '@angular/core';
import { NavController, AlertController, ToastController, Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Clipboard } from '@ionic-native/clipboard';

import { SocialSharing } from '@ionic-native/social-sharing';
@Component({
  selector: 'page-saved',
  templateUrl: 'saved.html'
})
export class SavedPage {
    
    public notes: Array<any>;
    public properties:any;
    public languages: Array<any>;

  constructor(public navCtrl: NavController, private storage: Storage, private alertCtrl: AlertController, private clipboard: Clipboard, private sharer: SocialSharing, private toastCtrl: ToastController, public events: Events) {
        this.properties = {noteDeleted:false,notePosition:0};
        this.languages = [];
        
        this.storage.get("languages").then((data) => {
            if (data){
                this.languages = data;
            }
        })
        
  }
  
    ionViewDidEnter() {
        this.storage.get("savedNotes").then((notes) => {
            this.notes = notes;
        });      
    }  
  
    public copyNote(note){
        console.log("copying");
        this.clipboard.copy(note).then(() => {
            let toast = this.toastCtrl.create({
              message: 'Note copied to clipboard',
              duration: 1000,
              position: 'bottom'
            }); 
            toast.present();           
        });
    }
    
    public shareNote(note){
        console.log("sharing");
        this.sharer.share(note);
    }
    
    public dragNote(ev,index,note){
        let element = ev._elementRef.nativeElement;
        element.style.opacity = 1 - (ev._openAmount / 200);
        this.properties.notePosition = ev._openAmount;
        if (ev._openAmount >= (100) && !this.properties.noteDeleted){
            this.properties.noteDeleted = true;
            setTimeout(() => {
                if (this.properties.notePosition < 100){return;}
                let id = note.id;
                this.storage.get("notes").then((data) => {
                    if (data){
                        console.log(data);
                        console.log(id);
                        for (var index in data){
                            if (data[index].id === id){
                                data[index].saved = false;
                                this.storage.set("notes",data);
                                this.events.publish('notes:updated', data);
                                break;
                            }
                        }
                    }
                });
                
                this.notes.splice(index,1);
                this.storage.set("savedNotes", this.notes);
                let toast = this.toastCtrl.create({
                  message: 'Note removed',
                  duration: 1000,
                  position: 'bottom'
                }); 
                toast.present();  
                this.properties.noteDeleted = false; 
            },500);          
        }
        
    }    
    
    public formatLanguage(code){
        let formatted = "";
        for (let language of this.languages){
            if (language.code === code){
                formatted = language.name;
                break;
            }
        }
        return formatted;
    }  
    
    public removeTranslation(index, translationArray){
        translationArray.splice(index,1);
        this.storage.set("savedNotes", this.notes);
    }      
}



