import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController } from 'ionic-angular';

@Component({
    selector: 'edit-text',
    templateUrl: 'edit-text.html'
})

export class EditTextModal {
    
    public text:string;
    
    constructor(public platform: Platform, public params: NavParams, public viewCtrl: ViewController) {
        this.text = params.data.text;

    }
    
    
    public saveText(){
        this.viewCtrl.dismiss(this.text);
    }
    
    public dismiss() {
        this.viewCtrl.dismiss();
    }
}






