import { Component } from '@angular/core';
import { NavController, Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';

@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html'
})
export class SettingsPage {
    
    public languages: Array<any>;
    public settings: any;

    constructor(public navCtrl: NavController, private storage: Storage, public events: Events) {
        this.languages = [];
        this.settings = {language:"en-NZ", translations:[], historyMax:10};

        this.storage.get("languages").then((data) => {
            if (data){
                this.languages = data;
                console.log(this.languages);
            }
        })

        this.storage.get("settings").then((data) => {
            if (data){
                this.settings = data;
            }
        })        
    }
    
    
    public updateSettings(){
        console.log(this.settings);
        this.storage.set("settings",this.settings);
        this.events.publish('settings:updated', this.settings);
    }
    

}



